<template>
  <div style="margin-top: 15px">
    <el-card>
      <div style="padding: 10px 0;  color: #1E90FF; font-size: 20px">
        歌手：{{audio[0].artist}}
      </div>
      <aplayer :music="audio[0]" :list="audio" :showlrc="true"></aplayer>
    </el-card>
  </div>
</template>
<script>
import aplayer from 'vue-aplayer';

export default {
  components: { aplayer },
  name: "Detail",
  data() {
    return {
      id: this.$route.query.id,
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},

      audio: {
        title: 'dome1', // 歌曲名字
        artist: '豆腐厂厂长', // 演唱者
        url: 'http://localhost:9090/file/21b8b1b5cff44a8ea4bb44ca1a1ab96d.mp3', // 音频文件url
        pic: '', // 演唱者封面
        lrc: '[00:00.00] (,,•́ . •̀,,) 刚刚开始学钢琴弹的hhhh', // 歌词或者歌词文件
        theme: '', // 歌曲主题
      },
      state: true
    }
  },
  created() {
    this.load()
  },
  methods: {
    load() {
      this.request.get("/music/singer/" + this.id).then(res => {
        this.audio = res.data
      });
    }
  }
}
</script>

<style scoped>
.item1 {
  padding: 10px;
  color: #666;
}
</style>